import { Bar } from "react-chartjs-2";
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import { GoogleAdsSimulated } from "../../utils/simulatedData";


ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

export const BarChart = () => {

    const listColors = ["rgba(75, 192, 192, 0.5)",'rgba(54, 162, 235, 0.5)','rgba(255, 206, 86, 0.5)','rgba(255, 99, 132, 0.5)']

    const nombresCampaas = GoogleAdsSimulated.campaas.map((campaa) => campaa.nombre);
    const impresionesCampaas = GoogleAdsSimulated.campaas.map((campaa) => campaa.impresiones);
    const clicsCampaas = GoogleAdsSimulated.campaas.map((campaa) => campaa.clics);
    const conversionesCampaas = GoogleAdsSimulated.campaas.map((campaa) => campaa.conversiones);
    const costoCampaas = GoogleAdsSimulated.campaas.map((campaa) => campaa.costo);

    const data = {
        labels: nombresCampaas,
        datasets: [
            {
              label: 'Impresiones',
              data: impresionesCampaas, // Datos para las impresiones
              backgroundColor: 'rgba(75, 192, 192, 0.5)',
              borderColor: 'rgba(75, 192, 192, 1)',
              borderWidth: 1,
            },
            {
              label: 'Clics',
              data: clicsCampaas, // Datos para los clics
              backgroundColor: 'rgba(54, 162, 235, 0.5)',
              borderColor: 'rgba(54, 162, 235, 1)',
              borderWidth: 1,
            },
            {
              label: 'Conversiones',
              data: conversionesCampaas, // Datos para las conversiones
              backgroundColor: 'rgba(255, 206, 86, 0.5)',
              borderColor: 'rgba(255, 206, 86, 1)',
              borderWidth: 1,
            },
            {
              label: 'Costo',
              data: costoCampaas, // Datos para el costo
              backgroundColor: 'rgba(255, 99, 132, 0.5)',
              borderColor: 'rgba(255, 99, 132, 1)',
              borderWidth: 1,
            },
          ],
    };
      
    const options = {
        responsive: true,
        plugins: {
        legend: {
            position: 'top' as const,
        },
        title: {
            display: true,
            text: 'Campaas Google Ads',
        },
        },
    };

    return(
        <Bar data={data} options={options} />
    );
}